.CollegeLogo{
    height: 100px;
    border-radius: 10px;
}
.custom-container{
    height: 80vh;
    width: 95vw;
    margin: 40px auto 0px auto;
    display: flex;
    flex-direction: column;
    overflow-x: scroll;
    color: #1a1e37;
}
.custom-container h3{
    align-self: flex-start;
}
.tableClass{
    margin-top: 50px;
    width: 80vw;
    height: 50vh;
    text-align: center;
}
.tableClass td{
    width: max-content;
    padding: 10px;
    height: 30%;
    margin: 10px;
    text-align: center;
}
.tableClass td a{
    text-decoration: none;
    color: black;
}
.aboutMyEducation{
    width: 95vw;
}